
:root {
  --color-white: #fff;
  --color-black: #000;
  --color-gray: #B4B4B4;
  --color-dark-gray: #434343;
  --color-red: #FC6D6B;
  --color-info: #1E6EF9;
  --color-success: #01dc87;
  --form-size: 40px;
  --font-size: 14px;
  --font-smallee: 12px;
  --font-family: "Proxima Nova", San Francisco, -apple-system, BlinkMacSystemFont, ".SFSNText-Regular", Segoe UI, Ubuntu, Helvetica, sans-serif;
}
* {
  box-sizing: border-box;
}
html, body {
  color: var(--color-black);
  font-family: var(--font-family);
  font-size: var(--font-size);
}
.container {
  max-width: 640px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}
.title-heading {
  margin-top: 24px;
  margin-bottom: 80px;
  text-align: center;
  font-weight: 300;
  font-size: 40px;
}
.button {
  font-size: var(--font-smallee);
  padding: 0 24px;
  height: var(--form-size);
  color: var(--color-white);
  line-height: calc(var(--form-size) - 1);
  font-family: var(--font-family);
  text-transform: uppercase;
  letter-spacing: 1px;
  background: var(--color-black);
  border: 1px solid transparent;
  cursor: pointer;
}
.button:hover {
  background: var(--color-dark-gray);
  transition: 100ms all ease;
}
.button:disabled {
  cursor: not-allowed;
}
.generate-action {
  text-align: center;
}
.form-input {
  display: block;
  width: 100%;
  padding: 0 8px;
  color: var(--color-dark-gray);
  height: var(--form-size);
  font-family: var(--font-family);
  line-height: calc(var(--form-size) - 1);
  border: 1px solid var(--color-gray);
}
.form-input:focus {
  outline: 0;
  border-color: var(--color-info);
}
.form-input.-danger {
  border-color: var(--color-red);
}
label span {
  font-size: var(--font-size);
  text-transform: uppercase;
}
.bar-width {
  display: inline-block;
  width: 60px;
  height: 25px;
}
.panel-box {
  margin-bottom: 64px;
}
.panel-box .item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 16px;
  margin-left: -40px;
  transition: 300ms all cubic-bezier(.55,0,.1,1);
}
.panel-box .item.sortable-chosen {
  opacity: 0.2;
}
.panel-box .item > .remove {
  display: inline-block;
  padding: 0 16px;
  color: var(--color-red);
  background: transparent;
  border: 1px solid transparent;
  cursor: pointer;
}
.panel-box .item > .handle {
  display: inline-block;
  padding: 8px 16px;
  color: var(--color-black);
  background: transparent;
  border: 1px solid transparent;
  cursor: -webkit-grab;
  cursor: grab;
}
.panel-box > .add {
  display: inline-block;
  padding: 0;
  color: var(--color-gray);
  background: transparent;
  border: 1px solid transparent;
  font-family: var(--font-family);
  cursor: pointer;
}
.preview-box {
  margin-bottom: 64px;
  padding: 8px;
  text-align: center;
  border: 1px solid var(--color-gray);
}
.menu-heading {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 24px;
  margin-right: 48px;
}
.menu-heading > .title {
  margin: 0;
  font-size: var(--font-smallee);
  text-transform: uppercase;
}



