
.dot-loader > div {
  display: inline-block;
  width: 8px;
  height: 8px;
  background-color: var(--color-white);
  border-radius: 50%;
  -webkit-animation: 0.5s infinite ease-in-out;
          animation: 0.5s infinite ease-in-out;
  -webkit-animation-name: pop;
          animation-name: pop;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}
.dot-loader > div:not(:last-child) {
  margin-right: 2px;
}
.dot-loader > div:nth-child(1) {
  -webkit-animation-delay: -0.32s;
          animation-delay: -0.32s;
}
.dot-loader > div:nth-child(2) {
  -webkit-animation-delay: -0.16s;
          animation-delay: -0.16s;
}
.dot-loader > div:nth-child(3) {
  -webkit-animation-delay: -0.048s;
          animation-delay: -0.048s;
}
@-webkit-keyframes pop {
0% { -webkit-transform: translateY(8px); transform: translateY(8px); opacity: 0.6;
}
80% { -webkit-transform: translateY(0px); transform: translateY(0px); opacity: 1;
}
}
@keyframes pop {
0% { -webkit-transform: translateY(8px); transform: translateY(8px); opacity: 0.6;
}
80% { -webkit-transform: translateY(0px); transform: translateY(0px); opacity: 1;
}
}



